// import threeLevelAddress from '@/asset/js/citys'
import { getChildrenByPId } from '@/api/userApi.js'
export default {
  name: 'selector',
  props: {
    show: {
      type: Boolean,
      default: false
    },
    isClick: {
      type: Boolean,
      default: false
    },
    close: {
      type: Function,
      default: null
    }
  },
  data() {
    return {
      regionInit: false,
      myAddressSlots: [
        // 省
        {
          flex: 1,
          values: [], // 省份数组
          className: 'slot1',
          textAlign: 'center'
        },
        // 分隔符
        {
          divider: true,
          content: '',
          className: 'slot2'
        },
        // 市
        {
          flex: 1,
          values: [],
          className: 'slot3',
          textAlign: 'center'
        },
        {
          divider: true,
          content: '',
          className: 'slot4'
        },
        // 县
        {
          flex: 1,
          values: [],
          className: 'slot5',
          textAlign: 'center'
        }
      ]
    }
  },
  mounted() {
    this.getProvinceArr(0)
  },
  methods: {
    // 调取地区列表的函数
    area(id) {
      let that = this
      let promise = new Promise(function(resolve, reject) {
        let data = {
          parentId: id
        }
        getChildrenByPId(data)
          .then(function (res) {
            resolve(res.data)
          }).catch(function (error) {
            console.log(error)
          })
      })
      return promise
    },
    areaChange(picker, values) {
      let that = this
      if (this.regionInit) {
        if (values[0]) {
          that.area(values[0]['id']).then(function(data) {
            picker.setSlotValues(1, data)
          })
        }
        if (values[1]) {
          that.area(values[1]['id']).then(function(data) {
            picker.setSlotValues(2, data)
          })
        }
      } else {
        this.regionInit = true
      }
      this.$emit('area', values)
    },
    triggerClose() {
      this.close()
    },
    // 遍历json，返回省级对象数组
    getProvinceArr(id) {
      let data = {
        parentId: id
      }
      getChildrenByPId(data).then(res => {
        if (res.code === '1000') {
          this.myAddressSlots[0].values = res.data
        }
      })
    }
  }
}
